import { RouterModule, Routes } from "@angular/router";
import { CoreRoutes, LayoutRoutes, RoomsPageName } from "@core/configs/routing-config";
import { StepsPageComponent } from "./pages/steps/steps-page.component";
import { NgModule } from "@angular/core";
import { RoomLayoutComponent } from "./room-layout/room-layout.component";
import { AnimationGuard } from "@core/guards/animation-guard/animation.guard";
import { SelectionGuard } from "@core/guards/selection-guard/selection.guard";
import { FloorPageComponent } from "./pages/steps/floor-page/floor-page.component";
import { ViewPageComponent } from "./pages/steps/view-page/view-page.component";
import { BathPageComponent } from "./pages/steps/bath-page/bath-page.component";
import { SleepPageComponent } from "./pages/steps/sleep-page/sleep-page.component";
import { RoomsMapPageComponent } from "./pages/steps/rooms-map-page/rooms-map-page.component";
import { NoisePageComponent } from "./pages/steps/noise-page/noise-page.component";
import { SpacePageComponent } from "./pages/steps/space-page/space-page.component";

const routes: Routes = [
  {
    path: LayoutRoutes.ROOMS,
    component: RoomLayoutComponent,
    canDeactivate: [AnimationGuard],
    children: [
      {
        path: CoreRoutes.ROOM,
        component: StepsPageComponent,
        canDeactivate: [AnimationGuard],
        canActivateChild: [SelectionGuard],
        children: [
          {
            path: RoomsPageName.FLOOR,
            component: FloorPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.VIEW,
            component: ViewPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.NOISE,
            component: NoisePageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.SPACE,
            component: SpacePageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.BATHE,
            component: BathPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.SLEEP,
            component: SleepPageComponent,
            canDeactivate: [AnimationGuard]
          },
          {
            path: RoomsPageName.ROOMS_MAP,
            component: RoomsMapPageComponent,
            canDeactivate: [AnimationGuard]
          },
        ]
      }
    ]
  }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RoomRoutingModule {}
