import { Component, OnInit } from '@angular/core';
import { AnimationBuilder } from "@angular/animations";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { ISelection } from "@shared/models/selection.model";
import { SelectionService } from "@core/services/selection-service/selection.service";
import { RoomsPageName } from "@core/configs/routing-config";

@Component({
  selector: 'app-space-page',
  templateUrl: './space-page.component.html',
  styleUrls: ['./space-page.component.scss']
})
export class SpacePageComponent extends AbstractAnimatedComponent implements OnInit {

  selectedRoomSpace: string;
  pageData: ISelection = {} as ISelection;
  allPagesLength: number;

  constructor(
      private builder: AnimationBuilder,
      private _selectionService: SelectionService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.selectedRoomSpace = this._selectionService.selectionDto[RoomsPageName.SPACE];
    this.allPagesLength = this._selectionService.selectionPages.length;
    this.pageData = this._selectionService.selectionPages.find(selection => selection.name == RoomsPageName.SPACE);
  }

  selectPageOption(optionName: string): void {
    this.selectedRoomSpace = optionName;
    this._selectionService.selectionDto = { selectionName: RoomsPageName.SPACE, selectionOption: optionName };
  }
}
