import { Component, OnInit } from '@angular/core';
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { RoomsPageName } from "@core/configs/routing-config";
import { ISelection } from "@shared/models/selection.model";
import { SelectionService } from "@core/services/selection-service/selection.service";

@Component({
  selector: 'app-bath-step',
  templateUrl: './bath-page.component.html',
  styleUrls: ['./bath-page.component.scss']
})
export class BathPageComponent extends AbstractAnimatedComponent implements OnInit {

  selectedBathType: string;
  pageData: ISelection = {} as ISelection;
  allPagesLength: number;

  constructor(
      private builder: AnimationBuilder,
      private _selectionService: SelectionService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.selectedBathType = this._selectionService.selectionDto[RoomsPageName.BATHE];
    this.allPagesLength = this._selectionService.selectionPages.length;
    this.pageData = this._selectionService.selectionPages.find(selection => selection.name == RoomsPageName.BATHE);
  }

  selectPageOption(optionName: string): void {
    this.selectedBathType = optionName;
    this._selectionService.selectionDto = { selectionName: RoomsPageName.BATHE, selectionOption: optionName };
  }
}
