import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { CoreRoutes, RoomsPageName } from "@core/configs/routing-config";
import { RoomsMapService } from "@core/services/rooms-map-service/rooms-map.service";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { IRoom } from "@shared/models/room.model";
import { UtilityService } from "@core/utilities/utility.service";
import { SelectionService } from "@core/services/selection-service/selection.service";
import { IGuest } from "@shared/models/guest.model";
import { takeUntil } from "rxjs/operators";
import { Subject } from "rxjs";
import { IHotel } from "@shared/models/hotel.model";
import { HotelService } from "@core/services/hotel-service/hotel.service";
import { GuestService } from "@core/services/guest-service/guest.service";
import { IReservation } from "@shared/models/reservation.model";

@Component({
  selector: 'app-rooms-map-page',
  templateUrl: './rooms-map-page.component.html',
  styleUrls: ['./rooms-map-page.component.scss'],
})
export class RoomsMapPageComponent extends AbstractAnimatedComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();

  isShowConfirmDialog: boolean = false;
  pageData: Array<IRoom>;
  guestData: IGuest = {} as IGuest;
  hotelData: IHotel = {} as IHotel;
  reservationData: IReservation = {} as IReservation;

  selectedRoomMap: IRoom = {} as IRoom;
  selectedRoom: IRoom = {} as IRoom;
  filteredRoomsList: Array<string>;

  constructor(
      private builder: AnimationBuilder,
      private router: Router,
      private _roomsMapService: RoomsMapService,
      private _selectionService: SelectionService,
      private _hotelService: HotelService,
      private _guestService: GuestService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.hotelData = this._hotelService.hotelData;
    this.guestData = this._guestService.guestData;
    this.reservationData = this._guestService.guestReservationData;

    this.subscribes();
  }

  subscribes(): void {
    this._roomsMapService.getAllRoomsById(this.hotelData.id)
        .pipe(
            takeUntil(this.destroy$)
        )
        .subscribe(res => {
          this.filteredRoomsList = this.filterRoomsBySelections(res);
          this.pageData = res;
          this.selectedRoom = this.pageData.find(room => this.filteredRoomsList.includes(room.roomNumber.toString()));
        });

    this._roomsMapService.isShowConfirmDialog$
        .pipe(
            takeUntil(this.destroy$)
        )
        .subscribe(value => this.isShowConfirmDialog = value);
  }

  filterRoomsBySelections(roomsList: Array <IRoom> ): Array <string> {
    let roomSelectedOptions = roomsList.reduce((acc, item) => {

      Object.entries(this._selectionService.selectionDto).forEach(([key, value]) => {
        if (item[key] === value) {
          acc[item.roomNumber] = (acc[item.roomNumber] || 1) + 1;
        }
      });

      return acc;
    }, {});

    let roomNumbersList = Object.keys(roomSelectedOptions);
    return roomNumbersList.sort((a, b) => roomSelectedOptions[b] - roomSelectedOptions[a]).slice(0, 3);
  }

  onToggleMobileRoomMap(room) {
    this.selectedRoomMap = room;
    this._roomsMapService.isShowMobileMapDialog$.next(!!room);
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
