import { Component, OnDestroy, OnInit } from '@angular/core';
import { CoreRoutes, RoomsPageName } from "@core/configs/routing-config";
import { NavigationEnd, Router } from "@angular/router";
import { RoomsMapService } from "@core/services/rooms-map-service/rooms-map.service";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { SelectionService } from "@core/services/selection-service/selection.service";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { IHotel } from "@shared/models/hotel.model";
import { HotelService } from "@core/services/hotel-service/hotel.service";

@Component({
  selector: 'app-steps-page',
  templateUrl: './steps-page.component.html',
  styleUrls: ['./steps-page.component.scss']
})
export class StepsPageComponent extends AbstractAnimatedComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();

  roomStepsList: Array<string>;
  currentRoomStep: string;
  nextRoomStep: string;
  prevRoomStep: string;
  hotelData: IHotel = {} as IHotel;

  public coreRoutes = CoreRoutes;
  public roomsPageName = RoomsPageName;
  public isShowConfirmDialog: boolean = false;
  public isShowMobileMapDialog: boolean = false;

  constructor(
      private builder: AnimationBuilder,
      private router: Router,
      private _roomsMapService: RoomsMapService,
      private _selectionService: SelectionService,
      private _hotelService: HotelService,
  ) {
    super(builder);
  }

  ngOnInit(): void {
    this.roomStepsList = this._selectionService.selectionPages.map(selection => selection.name);
    this.hotelData = this._hotelService.hotelData;

    this.setNavigationParams();
    this.subscribes();
  }

  subscribes(): void {
    this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
        this.setNavigationParams();
      }
    });
    this._roomsMapService.isShowConfirmDialog$
        .pipe(
            takeUntil(this.destroy$)
        ).subscribe(value => this.isShowConfirmDialog = value);

    this._roomsMapService.isShowMobileMapDialog$
        .pipe(
            takeUntil(this.destroy$)
        ).subscribe(value => this.isShowMobileMapDialog = value);
  }

  setNavigationParams(): void {
    const urls = this.router.url.split('/');
    let currentRoomStepIndex: number;

    this.currentRoomStep = urls[urls.length - 1];

    if (this.isLastRoomStep()) {
      this.prevRoomStep = this.roomStepsList[this.roomStepsList.length - 1];
      this.nextRoomStep = null;
      return;
    }

    currentRoomStepIndex = this.roomStepsList.indexOf(this.currentRoomStep);
    this.nextRoomStep = this.roomStepsList[currentRoomStepIndex + 1];
    this.prevRoomStep = this.roomStepsList[currentRoomStepIndex - 1]
  }

  isLastRoomStep(): boolean {
    return this.currentRoomStep === this.roomsPageName.ROOMS_MAP;
  }

  openConfirmDialog(): void {
    this._roomsMapService.isShowConfirmDialog$.next(true);
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
