import { Component, OnInit } from '@angular/core';
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { DataService } from "@core/services/data-service/data.service";
import { RoomsPageName } from "@core/configs/routing-config";
import { SelectionService } from "@core/services/selection-service/selection.service";
import { ISelection } from "@shared/models/selection.model";

@Component({
  selector: 'app-floor-step',
  templateUrl: './floor-page.component.html',
  styleUrls: ['./floor-page.component.scss']
})
export class FloorPageComponent extends AbstractAnimatedComponent implements OnInit {

  pageData: ISelection = {} as ISelection;
  selectedFloorType: string;
  allPagesLength: number;

  constructor(
      private builder: AnimationBuilder,
      private _selectionService: SelectionService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.selectedFloorType = this._selectionService.selectionDto[RoomsPageName.FLOOR];
    this.allPagesLength = this._selectionService.selectionPages.length;
    this.pageData = this._selectionService.selectionPages.find(selection => selection.name == RoomsPageName.FLOOR);
  }

  selectPageOption(optionName: string): void {
    this.selectedFloorType = optionName;
    this._selectionService.selectionDto = { selectionName: RoomsPageName.FLOOR, selectionOption: optionName };
  }
}
