import { Component, OnInit } from '@angular/core';
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { AnimationBuilder } from "@angular/animations";
import { RoomsPageName } from "@core/configs/routing-config";
import { ISelection } from "@shared/models/selection.model";
import { SelectionService } from "@core/services/selection-service/selection.service";

@Component({
  selector: 'app-dreams-location-page',
  templateUrl: './sleep-page.component.html',
  styleUrls: ['./sleep-page.component.scss']
})
export class SleepPageComponent extends AbstractAnimatedComponent implements OnInit {

  selectedRoomSleep: string;
  pageData: ISelection = {} as ISelection;
  allPagesLength: number;

  constructor(
      private builder: AnimationBuilder,
      private _selectionService: SelectionService
  ) {
    super(builder);
  }

  ngOnInit() {
    this.selectedRoomSleep = this._selectionService.selectionDto[RoomsPageName.SLEEP];
    this.allPagesLength = this._selectionService.selectionPages.length;
    this.pageData = this._selectionService.selectionPages.find(selection => selection.name == RoomsPageName.SLEEP);
  }

  selectPageOption(optionName: string): void {
    this.selectedRoomSleep = optionName;
    this._selectionService.selectionDto = { selectionName: RoomsPageName.SLEEP, selectionOption: optionName };
  }
}
